<script>
import { ref, watch, $emit, onMounted } from 'vue'
import { apiGetPinkWorkOrderInfo } from "@/api/api"
import { addZero, getUrlId,getUrlId2, monthHandle } from '@/utils/handle'



export default {
  setup(props, { emit }) {
    const workOrderId = getUrlId2().urlId
    const applyId = getUrlId().urlId
    const workInfoData = ref({})


    let today = new Date()
    let year = today.getFullYear()
    let month = today.getMonth() + 1

    let scheduleList = ref()
    let start = ref('')
    let end = ref('')
    let dateList = ref()
    let checkList = ref()
    let cancelList = ref()
    let clickDay = ref(today)
    const color = "purple"
    const attrs = ref([
      {
        highlight: {
          style: {
            background: "#ccc"
          },
          contentStyle: {
            color: "black"
          }
        },
        dates: dateList
      },
      {
        highlight: {
          color: "purple",
          fillMode: "solid",
          contentClass: "italic"
        },
        dates: checkList
      },
    ])

    const attributes = []

    const dayclick = (day) => {
      if (checkList.value.includes(day.id)) {
        // console.log("取消")
        checkList.value = checkList.value.filter(x => x != day.id)
      } else if (dateList.value.includes(day.id)) {
        // console.log("新增")
        checkList.value.push(day.id)
      }
      cancelList.value = dateList.value.filter(x => !checkList.value.includes(x))
      updateDay(checkList.value, cancelList.value)
    }

    const updateDay = (val, val2) => {
      emit("updateDay", val)
      emit("updateCancelDay", val2)
    }

    onMounted(async () => {
      workInfoData.value = await apiGetPinkWorkOrderInfo({applyId:applyId})
      workInfoData.value = workInfoData.value.data
      // console.log(workInfoData.value)
      dateList.value = getDateList(workInfoData.value.workdays)
      checkList.value = dateList.value
      updateDay(checkList.value, cancelList.value)
    })

    //把資料串起來return dateList
    const getDateList = (data) => {
      // console.log("把資料串起來")
      let dateList = []
      data.forEach(item => {
        dateList.push(item.start.split(' ')[0])
      })
      return dateList
    }


    return { attrs, dayclick, year, month }
  }
}
</script>

<template>
  <div class="testStyle2">
    <v-calendar class='date-picker' is-expanded :from-page="{ month: month, year: year }" :attributes="attrs"
      @dayclick="dayclick" />
  </div>
</template>

<style lang="scss" scoped>
.date-picker {
  border: 0px;
}
</style>