<script>
import { ref, onMounted } from 'vue'
import { useRouter } from "vue-router"
import Header from "../../components/global/header/HeaderArea.vue"
import WorkCalendar from '../order/components/WorkCalendar.vue'
import { getUrlId,getUrlId2, monthHandle, errorHandle,monthHandle3 } from '@/utils/handle'
import { apiGetPinkWorkOrderInfo, apiPostTakeJobApply } from "@/api/api"
import { Toast } from '@/components/global/swal'
import Swal from "sweetalert2"

export default {
  setup() {
    const router = useRouter()
    const workOrderId = getUrlId2().urlId
    const applyId = getUrlId().urlId
    
    
    let orderData = ref([])
    const postData = {
      "workOrderId": workOrderId,
      "isConfirm": 1,
      "checkTime": [],
      "cancelTime": [],
      "checkDays": [],
      "cancelDays": []
    }
    onMounted(async () => {
      try {
        orderData.value = await apiGetPinkWorkOrderInfo({applyId:applyId})
        orderData.value = orderData.value.data

        //處理日期
        let res = monthHandle(orderData.value.workdays)
        orderData.value.time = res.time
        //處理接受、取消、服務日期
        let res2 = monthHandle3(orderData.value.checkDate)
        orderData.value.checkDateInfo = res2.dateInfo
        let res3 = monthHandle3(orderData.value.cancelDate)
        orderData.value.cancelDateInfo = res3.dateInfo
        let res4 = monthHandle3(orderData.value.requireDate)
        orderData.value.requireDateInfo = res4.dateInfo
        // languageInfo
        orderData.value.employerService.languageInfo = []
        if (orderData.value.employerService.language?.length > 0) {
          orderData.value.employerService.language.forEach(item => {
            orderData.value.employerService.languageInfo.push(item.name)
          })
        }
        //licenseInfo
        orderData.value.employerService.licenseInfo = []
        if (orderData.value.employerService.license?.length > 0) {
          orderData.value.employerService.license.forEach(item => {
            orderData.value.employerService.licenseInfo.push(item.name)
          })
        }
        //eatTypeInfo
        // orderData.value.disabledPerson.eatTypeInfo = []
        // if (orderData.value.disabledPerson.eatType?.length > 0) {
        //   orderData.value.disabledPerson.eatType.forEach(item => {
        //     orderData.value.disabledPerson.eatTypeInfo.push(item.name)
        //   })
        // }
        //abilityToActInfo
        orderData.value.disabledPerson.abilityToActInfo = []
        if (orderData.value.disabledPerson.abilityToAct?.length > 0) {
          orderData.value.disabledPerson.abilityToAct.forEach(item => {
            orderData.value.disabledPerson.abilityToActInfo.push(item.name);
          })
        }
        //servicePlaceInfo
        if (orderData.value.disabledPerson.servicePlace.placeType.id == 1) {
          orderData.value.disabledPerson.servicePlaceInfo = orderData.value.disabledPerson.servicePlace.city.name + orderData.value.disabledPerson.servicePlace.area.name + " " + orderData.value.disabledPerson.servicePlace.place.address
        } else {
          orderData.value.disabledPerson.servicePlaceInfo = orderData.value.disabledPerson.servicePlace.place.name
        }
        //serviceInfo
        orderData.value.employerService.serviceInfo = []
        if (orderData.value.employerService.service?.length > 0) {
          orderData.value.employerService.service.forEach(item => {
            orderData.value.employerService.serviceInfo.push(item.name)
          })
        }
      } catch (error) {
        console.log(error)
      }
      // console.log(orderData.value)
    })

    const goto = (val) => {
      router.push({ path: val })
    }

    //點擊日期
    const updateDay = async (val) => {
      // console.log('updateDay', val)
      postData.checkDays = val
    }
    const updateCancelDay = async (val) => {
      // console.log('updateCancelDay', val)
      postData.cancelDays = val
    }

    //送出邀約
    const checkOrder = async () => {
      let start = orderData.value.workdays[0].start.split(' ')[1]
      let end = orderData.value.workdays[0].end.split(' ')[1]
      if (start != end) {
        postData.checkTime = dayToTime(postData.checkDays, start, end)
        if (postData.cancelDays) {
          postData.cancelTime = dayToTime(postData.cancelDays, start, end)
        }
      } else {
        postData.checkTime = dayToTime2(postData.checkDays, start, end)
        if (postData.cancelDays) {
          postData.cancelTime = dayToTime2(postData.cancelDays, start, end)
        }
      }
      // console.log("postData", postData)
      try {
        let msg = await apiPostTakeJobApply(postData)
        if (msg.status == true) {
          Toast("已回復邀約")
          orderData.value.replyStatus = 2
        } else {
          let errMessage = errorHandle(msg)
          Toast(errMessage)
        }
      } catch (error) {
        console.log(error)
      }
    }

    const dayToTime = (dayList, start, end) => {
      let array = []
      dayList.forEach(item => {
        array.push({
          "start": `${item} ${start}`,
          "end": `${item} ${end}`
        })
      })
      return array
    }
    const dayToTime2 = (dayList, start, end) => {
      let array = []
      dayList.forEach(item => {
        let tomorrow = new Date(Date.parse(item) + 1000 * 60 * 60 * 24)
        array.push({
          "start": `${item} ${start}`,
          "end": `${tomorrow.getFullYear()}-${tomorrow.getMonth() + 1}-${tomorrow.getDate()} ${end}`
        })
      })
      return array
    }
    //拒絕邀約
    const rejectOrder = async () => {
      postData.isConfirm = 0
      // console.log("postData", postData)
      try {
        let msg = await apiPostTakeJobApply(postData)
        if (msg.status == true) {
          Toast("已拒絕邀約")
          orderData.value.replyStatus = 3
        } else {
          let errMessage = errorHandle(msg)
          Toast(errMessage)
        }
      } catch (error) {
        console.log(error)
      }
    }

    return { orderData, goto, workOrderId, updateDay, updateCancelDay, checkOrder, rejectOrder }
  },
  components: {
    Header, WorkCalendar
  },
};

</script>

<template>
  <Header title="工單內容" backToPath="/order/list" />
  <div class="orderInfo">
    <section class="c-main">
      <div class="detail-container">
        <div class="item-container">
          <div class="item-title">照顧日期時段</div>
          <div class="item-content">
            <div class="main2" v-if="(orderData.replyStatus == 1)||(orderData.replyStatus == 0)">
              <div class="title2">服務日期</div>
              <WorkCalendar @update-day="updateDay" @update-cancel-day="updateCancelDay" />
            </div>
            <div class="main2" v-if="orderData.replyStatus == 2">
              <div class="title2">接受日期</div>
              <div class="content2">{{ orderData?.checkDateInfo.join('、') }}</div>
            </div>
            <div class="main2" v-if="orderData.replyStatus == 2">
              <div class="title2">取消日期</div>
              <div class="content2">{{ orderData?.cancelDateInfo.join('、') }}</div>
            </div>
            <div class="main2" v-if="orderData.replyStatus == 3">
              <div class="title2">服務日期</div>
              <div class="content2">{{ orderData?.requireDateInfo.join('、') }}</div>
            </div>
            <div class="main2">
              <div class="title2">服務時段</div>
              <div class="content2">{{ orderData?.time }}</div>
            </div>
            <div class="main2">
              <div class="title2">服務類別</div>
              <div class="content2">{{ orderData?.employerService?.name }}</div>
            </div>
          </div>
        </div>
        <div class="item-container" v-if="orderData?.disabledPerson">
          <div class="item-title item-link collapsed" data-bs-toggle="collapse" data-bs-target="#collapse2">被照護者資訊</div>
          <div class="item-content collapse" id="collapse2">
            <div>
              <div class="main1">
                <div class="title1">名稱</div>
                <div class="content1">{{ orderData.disabledPerson.name }}</div>
              </div>
              <div class="main1">
                <div class="title1">性別</div>
                <div class="content1">{{ orderData.disabledPerson.gender == 'Female' ? '女性' : '男性' }}</div>
              </div>
              <div class="main1">
                <div class="title1">年齡</div>
                <div class="content1">{{ orderData.disabledPerson.age }}歲</div>
              </div>
              <div class="main1">
                <div class="title1">身高</div>
                <div class="content1">{{ orderData.disabledPerson.height }}公分</div>
              </div>
              <div class="main1">
                <div class="title1">體重</div>
                <div class="content1">{{ orderData.disabledPerson.weight }}公斤</div>
              </div>
              <div class="main1">
                <div class="title1">關係</div>
                <div class="content1">{{ orderData.disabledPerson.relationship }}</div>
              </div>
              <!-- <div class="main2">
                <div class="title2">是否有傳染力</div>
                <div class="content2">{{ orderData.disabledPerson.isContagious ? "是" : "否" }}</div>
              </div>
              <div class="main2">
                <div class="title2">飲食類型</div>
                <div class="content2" v-if="(orderData?.disabledPerson?.eatTypeInfo?.length != 0)">{{
                    orderData?.disabledPerson?.eatTypeInfo?.join('、')
                }}</div>
                <div class="content2" v-if="(orderData?.disabledPerson?.eatTypeInfo?.length == 0)">無</div>
              </div> -->
              <div class="main2">
                <div class="title2">行動能力</div>
                <div class="content2" v-if="(orderData?.disabledPerson?.abilityToActInfo?.length != 0)">{{
                    orderData?.disabledPerson?.abilityToActInfo?.join('、')
                }}</div>
                <div class="content2" v-if="(orderData?.disabledPerson?.abilityToActInfo?.length == 0)">無</div>

              </div>
              <div class="main2">
                <div class="title2">照顧地點</div>
                <div class="content2">{{ orderData.disabledPerson.servicePlaceInfo }}</div>
              </div>
            </div>
          </div>
        </div>
        <div class="item-container">
          <div class="item-title item-link collapsed" data-bs-toggle="collapse" data-bs-target="#collapse3">病症</div>
          <div class="item-content collapse" id="collapse3">
            <div class="main2" v-for="item in orderData?.disabledPerson?.disease" :key="item.id">
              <div class="title2">{{ item.diseaseName }}</div>
              <div class="content2">{{ item.symptom.join('、') }}</div>
            </div>
          </div>
        </div>
        <div class="item-container" v-if="orderData?.employerService">
          <div class="item-title item-link collapsed" data-bs-toggle="collapse" data-bs-target="#collapse4">服務需求</div>
          <div class="item-content collapse" id="collapse4">
            <div class="content2">{{ orderData.employerService?.serviceInfo?.join('、') }}</div>
          </div>
        </div>
        <div class="mt-4b btn-container">
          <div class="row" v-if="(orderData.replyStatus == 1)||(orderData.replyStatus == 0)">
            <div class="col">
              <button class="btn btn-outline-primary" type="button" @click="checkOrder">回復邀約</button>
            </div>
            <div class="col">
              <button class="btn btn-outline-primary" type="button" @click="rejectOrder">拒絕邀約</button>
            </div>
          </div>
          <div class="row" v-if="(orderData.replyStatus == 2) && (orderData.workOrderStatus == 2)">
            <div class="col">
              <button class="btn btn-primary" type="button">等待承接中</button>
            </div>
          </div>
          <div class="row" v-if="orderData.replyStatus == 3">
            <div class="col">
              <button class="btn btn-primary" type="button">已拒絕邀約</button>
            </div>
          </div>
          <div class="row" v-if="orderData.workOrderStatus == 3">
            <div class="col">
              <button class="btn btn-primary" type="button">執行中</button>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>



</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";

.orderInfo {
  margin-top: $header-height;
  margin-bottom: calc($footer-height + 15px);
}

.main {
  padding-top: 22px;
  padding-left: 22px;
  padding-right: 22px;
}

.main1 {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-top: 5px;
  margin-bottom: 5px;

  .title1 {
    font-size: 14px;
    font-weight: 400;
    color: #909090;
    margin-right: 14px;

  }

  .content1 {
    font-size: 16px;
    font-weight: 400;
    color: #333333;
  }
}

.main2 {
  margin-top: 10px;
  margin-bottom: 10px;

  .title2 {
    font-size: 14px;
    font-weight: 400;
    color: nth($secondary-color, 1);
  }

  .content2 {
    font-size: 16px;
    font-weight: 400;
    color: #333333;
  }

}
</style>